export default {
	data() {
		return {
			isActive: true,
			imageSrc: ''
		}
	},
	methods: {
		chooseImage() {
			let that = this;
			uni.chooseImage({
				success(e) {
					console.log(e);
					that.isActive = false;
					that.imageSrc = e.tempFilePaths[0];
					console.log(that.imageSrc);
				}
			})
		}
	}
}
